import React , {useState, useEffect}from 'react';
import {Form, Modal, Input, Select, message, Table} from 'antd';
import Layout from '../components/layout/layout';
import axios from 'axios';
import {EditOutlined, DeleteOutlined} from '@ant-design/icons'


const HomePage =() =>{

    const [showModal, setShowModal] = useState(false)
    const [allTransaction, setAllTransaction] = useState([]);
    const [frequency, setFrequency] = useState('7')

    const [editable, setEditable ] = useState(null)


    //table data

    const columns = [

        {
            title:'Date',
            dataIndex:'date'
        },

        {
            title:'Amount',
            dataIndex:'amount'
        },

        {
            title:'Type',
            dataIndex:'type'
        },

        {
            title:'Category',
            dataIndex:'category'
        },

        {
            title:'Reference',
            dataIndex:'reference'
        },

        {
            title:'Actions',
            render : (text,record) => (

                <div>
                    <EditOutlined  onClick={() => {
setEditable(record)
setShowModal(true)

                    }  }/>
                    <DeleteOutlined className = 'mx-2' onClick={() => {handleDelete(record)}}/>
                </div>
            )
            
        },

    ];
//get all transaction

const getAllTransactions = async () =>{
try{

    const user = JSON.parse(localStorage.getItem('user'));
    const res=  await axios.post('api/v1/transaction/get-transaction', {
    userid: user._id,
    frequency
});
    setAllTransaction(res.data);
      console.log(res.data);

}
catch(error){

    console.log(error);
    message.error('Fetch Issue With Transaction')

}

//hook





}

useEffect(() => {

    getAllTransactions();
    

}, []);


const handleDelete =  async(record) =>{
try{

    await axios.post('api/v1/transaction/delete-transaction',{
        transactionId: record._id});
        message.success("Transaction Deleted!");

}
catch(error)
{
    console.log(error)
    message.error('unable to delete')
}





}

    //form handling
    const handleSubmit =  async (values) =>{

        try{

            const user = JSON.parse(localStorage.getItem('user'));

           if(editable){
            await axios.post('api/v1/transaction/edit-transaction', {
                payload:{
                    ...values,
                    userid: user._id
                },
                transactionId: editable._id
            })
            message.success('Transaction Updated SuccesFully')

           }
           else{
            await axios.post('api/v1/transaction/add-transaction', {...values, userid: user._id})
            message.success('Transaction Added SuccesFully')
           }
            setEditable(null);
        }
        catch (error){

            message.error('Failed To Add Transaction')
        }
        
    }
return(

   <Layout>
<div className='filters'>

<div> 

    
</div>

    <button className='btn btn-primary' onClick={() => setShowModal(true) }>
Add New

    </button>
</div>


<div className='content'>
    <Table columns={columns}  dataSource={allTransaction}/>
</div>
  

<Modal title={editable ? 'Edit Transaction' : 'Add Transaction'}

open={showModal}

 onCancel={() => setShowModal(false)}
 footer={false}
 >
   <Form layout='vertical' onFinish={handleSubmit} initialValues={editable}>

<Form.Item label="Amount" name="amount">

<Input type="text" />

</Form.Item>

<Form.Item label ="Type" name="type"> 

<Select>
    <Select.Option value="income"> Income</Select.Option>
    <Select.Option value="expense"> Expense</Select.Option>
</Select>

</Form.Item>

<Form.Item label ="Category" name="category"> 

<Select>
    <Select.Option value="salary"> Salary</Select.Option>
    <Select.Option value="tip"> Tip</Select.Option>
    <Select.Option value="project"> Project</Select.Option>
    <Select.Option value="food"> Food</Select.Option>
    <Select.Option value="movie"> Movie</Select.Option>
    <Select.Option value="bills"> Bills</Select.Option>
    <Select.Option value="medical"> Medical</Select.Option>
    <Select.Option value="fee"> Fee</Select.Option>
    <Select.Option value="tax"> Tax</Select.Option>
</Select>

</Form.Item>

<Form.Item label="Date" name="date">

<Input type="date" />

</Form.Item>


<Form.Item label="Reference" name="reference">

<Input type="text" />

</Form.Item>


<Form.Item label="Description" name="description">

<Input type="text" />

</Form.Item>
<div className='d-flex justify-content-end'>

<button  type='submit'className='btn btn-primary'> SAVE</button>
</div>


   </Form>


</Modal>
</Layout>
   
)


}

export default HomePage